@font-face {
    font-family:'Questrial';
    src: url(../../images/Questrial-Regular.ttf);
  }
.form{
    margin: 10px;
}
.title_note{
    width: 100%;
    height: 10vh;
    border: 0;
    padding: 2%;
    font-weight: bold;
    color: rgba(0, 0, 0, 0.7);
    display: flex;
    align-items: center;
    font-family: 'Questrial';
    font-size: 20px;
}


.body_note{
    width: 100%;
    height: 60vh;
    border: 0;
    margin-top: 1%;
    padding: 2% 2% 0 2%;
    color: rgba(0, 0, 0, 0.7);
    font-family: 'Questrial';
    font-size: 20px;
    
}
.box{
    box-shadow: 0 0 10px 1px rgba(39, 37, 37, 0.2);
}
.btn_save {
    margin-right: 1%;
    padding-top: 2%;
    display: flex;
    justify-content: flex-end;
}

@media only screen 
and (min-device-width: 768px) 
and (max-device-width: 1024px) 
and (orientation: portrait) 
and (-webkit-min-device-pixel-ratio: 1) {
    .body_note{
        
        height: 70vh;
    
    }
}